.ColorPicker{
    width: 400px;
    padding: 10px;
    border: 1px solid black;

}

.ColorPicker_title{
    line-height: 1.3;
}

.ColorPicker__option{
    display: inline-block;
    width: 40px;
    height: 40px;
    margin: 4px;
    border: none;
    outline: none;
    cursor: pointer;

    transition: transform 250ms linear;
}

.ColorPicker__option:active{
    transform: scale(1.1);
}